import helmet from "../assets/images/helmet_1920.jpg"

const ShortRules = () => {
	return (
		<>
			<div className="row featurette">
				<div className="col-md-7 order-md-2">
					<h2 className="featurette-heading fw-normal lh-1 mt-4"><span className="text-primary">PRAVIDLA NA HITI.</span> <span
						className="text-body-secondary">Vtah z pravidel Bullet Farm.</span></h2>
					<ul className="lead bullet p-0 pt-2">
						<li>Stelba mimo hru je zakzan. Mimo hru je zbra zajitna.</li>
						<li>Povinnost nosit ochrann brle uren pro airsoft (sovan jsou zakzan) po celou dobu pohybu v prostoru a to i mimo hru. Pod 15 let povinnost celoobliejov ochrany.</li>
						<li>Stelba pouze jednotliv (vjimky udluje organiztor).</li>
						<li>Mrtvoly nemluv, a to ani pes vyslaku.</li>
						<li>Zakzna stelba naslepo - hr me stlet pouze na cle, kter se nachzej v jeho zornm poli.</li>
						<li>Stelba pes mal otvory pokud pes otvor lze prostrit hlava NEBO hr odstoup cca 1,5 m od pekky.</li>
						<li>Grant platn na cca 3 m od msta iniciace, pevn pekka zsahu zamez, hr zvol pekka a pesune se na jin msto. Granty si sbr jejich majitel.</li>
						<li>Zsahy jsou platn do jakkoliv sti tla nebo vstroje.</li>
						<li>Zsah do zbran vyazuje zasaenou zbra, hr zvol zbra a do nvratu na mrtvolit nesm zasaenou
							zbra pouvat.</li>
						<li>Pi zasaen hr VYTAHUJE LUTOU reflexn vestu, schov ji a po nvratu na mrtvolit.</li>
						<li>ZKAZ demolovn, penen, barikdovn a manipulace s pekkami.</li>
					</ul>
					<div className="d-flex justify-content-end">
						<a href="#/pravidla" className="w-40 btn btn-lg btn-primary border-primary text-shadow">Kompletn pravidla</a>
					</div>
				</div>
				<div className="col-md-5 order-md-1 mt-4">
					<img src={helmet} alt="" className="featurette-image img-fluid mx-auto" width="500" height="500"
						role="img" focusable="false" />
				</div>
			</div>
		</>
	)
}

export default ShortRules