import { useState } from 'react';
import "./ControlledCarousel.css"
import Carousel from 'react-bootstrap/Carousel';
import img1 from '../assets/images/military_1920.jpg'
import img2 from '../assets/images/military-g385_1920.jpg'
import img3 from '../assets/images/airsoft-replica_1920.jpg'



function ControlledCarousel() {
	const [index, setIndex] = useState(0);

	const handleSelect = (selectedIndex) => {
		setIndex(selectedIndex);
	};

	return (
		<Carousel activeIndex={index} onSelect={handleSelect}>
			<Carousel.Item>
				<img src={img1} className='d-block w-100 cover' alt="img1" />
				<Carousel.Caption className='text-primary'>
					<h3>Vtejte v Dynamitce!</h3>
					<p>Jsme airsoftov CQB hit, nov pesunuto v Bohnicch.</p>
				</Carousel.Caption>
			</Carousel.Item>
			<Carousel.Item>
				<img src={img2} className='d-block w-100 cover' alt="img2" />
				<Carousel.Caption className='text-primary'>
					<h3>Pij si zahrt!</h3>
					<p>A u jsi zatenk nebo zkuen hr, jsi u ns vtn.</p>
				</Carousel.Caption>
			</Carousel.Item>
			<Carousel.Item>
				<img src={img3} className='d-block w-100 cover' alt="img3" />
				<Carousel.Caption className='text-primary'>
					<h3>Co dlme?</h3>
					<p>Organizujeme pravideln akce, tmov soute a nabzme vybaven k pronjmu.</p>
				</Carousel.Caption>
			</Carousel.Item>
		</Carousel>
	);
}

export default ControlledCarousel;